<template>
    <div class="box" @mouseover="showVideo" @mouseout="closeVideo">
        <div class="videoBox" ref="childIndex">
            <div>{{videotitle}}</div>
            <div>up主:{{up}}</div>
            <div>播放:{{count}}</div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'videoBox',
        data() {
            return {
                videotitle:'雅戈泰之女的虚虚实实',
                up: '削人棍专用BGM',
                count: '17.3万'
            }
        },
        methods:{
            showVideo(){                
                var that = this
                setTimeout(function(){
                    that.$refs.childIndex.style.bottom='0px'
                },300)
            },
            closeVideo(){
                var that = this
                setTimeout(function(){
                    that.$refs.childIndex.style.bottom='-75px'
                },300)
            }
        },
        props:['childIndex']

    }
</script>
<style scoped>
    .box{
        width: 160px;
        height: 100px;
        background-color: darkcyan;
        display: inline-block;
        border-radius: 5px;
        text-align: left;
        font-size: 14px;
        position: relative;
        overflow: hidden;
    }
    .box>div{
        position: absolute;
        bottom:-75px;
    }
    .box>div>div{
        text-indent: 5px;
    }
    .box>div>div:nth-child(2){
        margin-top: 40px;
    }

</style>


