<template>
    <div>
        <ul>
            <li v-for="(li,index) in 20">
                <div>
                    <span></span>
                    <span>
                        <div class="search_title">【火影/鬼与雪】白：呐，再不斩先生也有重要的人吗？</div>
                        <div class="search_detail">-</div>
                        <div class="author">
                            <span>柒寒banana</span>
                            <span>0</span>
                            <span>0</span>
                        </div>
                    </span>
                </div>
            </li>
        </ul>
        <div style="clear: both;"></div>
        <div class="pagelist">
            <span v-show="nowpage>1" @click="behindPage">上一页</span>
            <span @click="gofirst" ref="pageone">1</span>
            <span v-show="nowpage>=5" class="pagedisable">...</span>
            <span class="pagebox" @click="changePage">2</span>
            <span class="pagebox" @click="changePage">3</span>
            <span class="pagebox" @click="changePage">4</span>
            <span class="pagebox" @click="changePage">5</span>
            <span v-show="nowpage>=5&&nowpage<allpage-3" class="pagebox" @click="changePage">{{nowpage+2}}</span>
            <span v-show="nowpage<allpage-3" class="pagedisable">...</span>
            <span @click="golast" ref="pageend">{{this.allpage}}</span>
            <span v-show="nowpage!=allpage" @click="nextPage">下一页</span>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'searchlist',
        data() {
            return {
                allpage: 10,//所有的页面
                nowpage: 1,//记录当前页
                pagetype: 1

            }
        },
        methods: {
            changePage(e) {
                $(e.currentTarget).text()
                this.nowpage = $(e.currentTarget).text()
                this.nowpage = parseInt(this.nowpage)
                this.showPageStyle();
            },
            gofirst(e) {
                $(e.currentTarget).text()
                this.nowpage = $(e.currentTarget).text()
                this.nowpage = parseInt(this.nowpage)
                let pagelist = $('.pagebox')
                for (var i = 0; i < pagelist.length; i++) {
                    $(pagelist[i]).text(this.nowpage + i + 1)
                }
                $('.pagelist>span').removeClass('blue_buttom')
                $(e.currentTarget).addClass('blue_buttom')
                this.pagetype = 1;

            },
            golast(e) {
                $(e.currentTarget).text()
                this.nowpage = $(e.currentTarget).text()
                this.nowpage = parseInt(this.nowpage)
                let pagelist = $('.pagebox')
                for (var i = 0; i < pagelist.length; i++) {
                    $(pagelist[i]).text(this.nowpage + i - 4)
                }
                $('.pagelist>span').removeClass('blue_buttom')
                $(e.currentTarget).addClass('blue_buttom')
                this.pagetype = 3;

            },
            nextPage() {
                if (this.nowpage == this.allpage - 1) {
                    this.nowpage++
                    $('.pagelist>span').removeClass('blue_buttom')
                    let that = this.$refs.pageend
                    $(that).addClass('blue_buttom')
                } else {
                    this.nowpage++
                    this.showPageStyle();
                    console.log(this.nowpage)
                }
            },
            behindPage() {
                if (this.nowpage == 2) {
                    this.nowpage--
                    $('.pagelist>span').removeClass('blue_buttom')
                    let that = this.$refs.pageone
                    $(that).addClass('blue_buttom')
                } else {
                    this.nowpage--
                    this.showPageStyle();

                }
            },
            showPageStyle() {
                if (this.pagetype == 1) {
                    if (this.nowpage < 5) {
                        $('.pagelist>span').removeClass('blue_buttom')
                        let pagelist = $('.pagebox')
                        $(pagelist[this.nowpage - 2]).addClass('blue_buttom')

                    } else {
                        let pagelist = $('.pagebox')
                        for (var i = 0; i < pagelist.length; i++) {
                            $(pagelist[i]).text(this.nowpage + i - 2)
                        }
                        $('.pagelist>span').removeClass('blue_buttom')
                        $(pagelist[2]).addClass('blue_buttom')
                        this.pagetype = 2;

                    }
                } else if (this.pagetype == 2) {
                    if (this.nowpage < 4) {
                        let pagelist = $('.pagebox')
                        for (var i = 0; i < pagelist.length; i++) {
                            $(pagelist[i]).text(this.nowpage + i - 1)
                        }
                        $('.pagelist>span').removeClass('blue_buttom')
                        $(pagelist[1]).addClass('blue_buttom')
                        this.pagetype = 1;

                    } else if (this.nowpage == 4) {
                        let pagelist = $('.pagebox')
                        for (var i = 0; i < pagelist.length; i++) {
                            $(pagelist[i]).text(this.nowpage + i - 2)
                        }
                        $('.pagelist>span').removeClass('blue_buttom')
                        $(pagelist[2]).addClass('blue_buttom')
                        this.pagetype = 1;

                    } else if (this.nowpage > 4 && this.nowpage < this.allpage - 3) {
                        let pagelist = $('.pagebox')
                        for (var i = 0; i < pagelist.length; i++) {
                            $(pagelist[i]).text(this.nowpage + i - 2)
                        }
                        $('.pagelist>span').removeClass('blue_buttom')
                        $(pagelist[2]).addClass('blue_buttom')

                    } else if (this.nowpage == this.allpage - 3) {
                        let pagelist = $('.pagebox')
                        for (var i = 0; i < pagelist.length; i++) {
                            $(pagelist[i]).text(this.nowpage + i - 1)
                        }
                        $('.pagelist>span').removeClass('blue_buttom')
                        $(pagelist[1]).addClass('blue_buttom')
                        this.pagetype = 3;

                    } else if (this.nowpage > this.allpage - 3) {
                        let pagelist = $('.pagebox')
                        for (var i = 0; i < pagelist.length; i++) {
                            $(pagelist[i]).text(this.nowpage + i - 2)
                        }
                        $('.pagelist>span').removeClass('blue_buttom')
                        $(pagelist[2]).addClass('blue_buttom')
                        this.pagetype = 3;

                    }
                } else if (this.pagetype == 3) {
                    if (this.nowpage > this.allpage - 4) {
                        $('.pagelist>span').removeClass('blue_buttom')
                        let pagelist = $('.pagebox')
                        $(pagelist[this.nowpage - this.allpage + 4]).addClass('blue_buttom')

                    } else {
                        let pagelist = $('.pagebox')
                        for (var i = 0; i < pagelist.length; i++) {
                            $(pagelist[i]).text(this.nowpage + i - 2)
                        }
                        $('.pagelist>span').removeClass('blue_buttom')
                        $(pagelist[2]).addClass('blue_buttom')
                        this.pagetype = 2;

                    }
                }
            }
        }
    }
</script>
<style scoped>
    ul {
        margin: 0;
        padding: 0;
    }

    li {
        list-style: none;
        width: 49%;
        float: left;
        border-bottom: 1px solid #ccc;
    }

    li>div {
        padding: 20px 30px 20px 0;
        display: inline-block;
        border-right: 1px solid #ccc;
    }

    li:nth-child(2n)>div {
        border-right: 1px solid white !important;
        padding: 20px 30px 20px 0;
        display: inline-block;
    }

    li>div>span:first-child {
        margin-left: 10px;
        width: 114px;
        height: 70px;
        background-color: aquamarine;
        border-radius: 5px;
        display: inline-block;
    }

    li>div>span:nth-child(2) {
        width: 190px;
        display: inline-block;
        vertical-align: top;
    }

    .search_title {
        font-size: 14px;
        text-align: left;
        padding-left: 3px;
    }

    .search_detail {
        font-size: 14px;
        text-align: left;
        padding-left: 3px;
        text-indent: 1em;
        height: 50px;
    }

    .author {
        text-align: left;
    }

    .author>span {
        font-size: 12px;
        text-align: left;
        padding-right: 10px;
    }

    .pagelist {
        padding: 20px 0;
        text-align: left;
    }

    .pagelist>span {
        font-size: 12px;
        display: inline-block;
        padding: 5px 10px;
        border: 1px solid #ccc;
        border-radius: 5px;
        vertical-align: top;
        line-height: 20px;
        cursor: pointer;
    }

    .pagedisable {
        font-size: 12px;
        display: inline-block;
        padding: 5px 10px;
        border: none !important;
        border-radius: 5px;
        vertical-align: top;
        line-height: 20px;
        user-select: none;
    }

    .blue_buttom {
        border: 1px solid #6cc6e4 !important;
        color: #6cc6e4;
    }
</style>


