<template>
    <div>
        <ul ref="guide">
            <li v-for="(list,index) in guideList" @mouseover="highColor($event)" @mouseout="lowColor($event)" @click="jumpto(index)">
                <a :href="guideList[index].tohere">
                    {{guideList[index].title}}
                </a>
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: 'guide',
        data() {
            return {
                guideList: [{ title: '直播', tohere: '#' },
                { title: '动画', tohere: '#' },
                { title: '番剧', tohere: '#' },
                { title: '国创', tohere: '#' },
                { title: '音乐', tohere: '#' },
                { title: '音频', tohere: '#' },
                { title: '舞蹈', tohere: '#' },
                { title: '游戏', tohere: '#' },
                { title: '科技', tohere: '#' },
                { title: '生活', tohere: '#' },
                { title: '鬼畜', tohere: '#' },
                { title: '时尚', tohere: '#' },
                { title: '广告', tohere: '#' },
                { title: '娱乐', tohere: '#' },
                { title: '电影', tohere: '#' },
                { title: 'TV剧', tohere: '#' },
                { title: '影视', tohere: '#' },
                { title: '纪录片', tohere: '#' },
                { title: '', tohere: '#' },
                { title: 'TOP', tohere: '#' }],
                scrollList: [],
            }
        },
        methods: {
            highColor(e) {
                $(e.currentTarget).css({
                    background: 'deepskyblue'
                })
                $(e.currentTarget).children().css({
                    color: 'white'
                })
            },
            lowColor(e) {
                $(e.currentTarget).css({
                    background: 'transparent'
                })
                $(e.currentTarget).children().css({
                    color: 'black'
                })
            },
            jumpto(index,e) {
                let jump = $('._tohere')
                let obj = jump[index];
                let total = jump[index].offsetTop
                while (obj = obj.offsetParent) {
                    total += obj.offsetTop;
                }
                //循环到没有父元素将所有的offsetTop加起来
                $('html').animate({
                    scrollTop: total
                }, 600)
                
            }
        },
        mounted() {
            let guide = this.$refs.guide;
            $(guide).children().eq(-3).css({
                'border-bottom': '1px solid #ccc'
            })
            $(guide).children().eq(-2).css({
                width: '30px',
                height: '10px',
                margin: ' 0 15px '
            })
            // $(guide).children().eq(-2).on("click", function (e) {
            //     e.preventDefault();
            // })
            // $(guide).children().eq(-2).on("click", function (e) {
            //     e.preventDefault();
            // })
            // $(guide).children().eq(-2).unbind("mouseover mouseout")
            $(guide).children().eq(-1).on('click',function(){
                $('html').animate({
                    scrollTop: 0
                }, 1000)
            })
            //将各个锚点对应的scrollTop存进数组内
            let _this = this;
            $('._tohere').each(function (element) {
                element = this
                let obj = element;
                let total = element.offsetTop;
                while (obj = obj.offsetParent) {
                    total += obj.offsetTop;
                }
                _this.scrollList.push(total)
            });
            //监听页面滚动
            window.addEventListener("scroll", function () {
                let scrolled = document.documentElement.scrollTop
                _this.scrollList.forEach((element, index) => {
                    if (scrolled >= element&&scrolled<_this.scrollList[(index+1)]) {
                        $($(guide).children()[index]).addClass('activeColor')
                        $($(guide).children()[index]).siblings().removeClass('activeColor')
                    }
                });
            })
        },
    }
</script>
<style scoped>
    ul {
        margin: 0;
        padding: 0;
        border-top: 1px solid #cccccc;
        border-radius: 10px;
        border-bottom: 1px solid #cccccc;
        overflow: hidden;

    }

    li {
        width: 60px;
        list-style: none;
        background-color: #f6f9fa;
        border-left: 1px solid #cccccc;
        border-right: 1px solid #cccccc;
    }

    li a {
        font-size: 14px;
        line-height: 35px !important;
    }
    .activeColor{
        background: deepskyblue!important
    }
    .activeColor a{
        color: white!important
    }
</style>


