<template>
  <div class="home">
    <label-bar-components></label-bar-components>
    <div style="padding-left: 8px;margin-top: 30px;" class="_tohere">
      <div style="width: 720px; display: inline-block;">
        <div class="videolist2">
          <div style="text-align: left;padding-bottom: 8px">最新动态</div>
          <div>
            <video-box2-components v-for="(video,index) in 4" :key="index" :child-index="index" style="float: left;"></video-box2-components>
          </div>
        </div>
        <search-list-components></search-list-components>
      </div>
      <right-nav-components :num='7'></right-nav-components>
    </div>
  </div>
</template>

<script>
  import labelBarComponents from '../components/label_bar.vue'
  import videoBox2Components from '../components/videoBox2.vue'
  import rightNavComponents from '../components/rightnav.vue'
  import searchListComponents from '../components/searchList.vue'
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: '假装这是音频页面！'
      }
    },
    components: { labelBarComponents ,videoBox2Components,rightNavComponents,searchListComponents}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .home {
    position: relative;
  }

  .home>div {
    min-width: 980px;
    display: block;
  }
  .videolist2 {
    display: inline-block;
    width: 720px;
    overflow: hidden;
  }
</style>


