<template>
    <div class="box">
        <div class="title">{{msg}}</div>
        <div class="videobox">
            <div v-for="(box,index) in 16">
                <div></div>
                <div>
                    <div>Four of a Kind 四牌士</div>
                    <div>
                        更新至
                        <div v-if="index<5" class="redColor">{{count}}话</div>
                        <div v-else class="greyColor">{{count}}话</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'typelist',
        data() {
            return {
                msg: '番剧',
                count: 22,
            }
        }
    }
</script>
<style scoped>
    .box {
        width: 700px;
        text-align: left;
        display: inline-block;
        margin-right: 20px;
    }

    .title {
        padding-bottom: 8px;
    }

    .videobox {
        width: 100%;
        height: 400px;
        overflow: scroll;
        overflow-x: hidden;
    }
    .videobox::-webkit-scrollbar {
        width: 10px;
    }

    .videobox::-webkit-scrollbar-button {
        height: 0px;
        background-color: white;
    }
    .videobox::-webkit-scrollbar-track{
        background: #dddddd;
    }
    .videobox::-webkit-scrollbar-thumb{
        background: #e5e9ef;
    }
    .videobox>div {
        display: inline-block;
        width: 33%;
        margin-bottom: 32px;
    }

    .videobox>div>div:first-child {
        width: 72px;
        height: 72px;
        background: cornflowerblue;
        margin-right: 8px;
        display: inline-block;
        border-radius: 5px;
    }

    .videobox>div>div:nth-child(2) {
        display: inline-block;
        vertical-align: top;
        width: 60%;
        font-size: 12px
    }

    .videobox>div>div:nth-child(2)>div:first-child {
        height: 50px;
    }

    .videobox>div>div:nth-child(2)>div:nth-child(2)>div {
        display: inline-block;
        color: white;
        padding: 2px 5px;
        border-radius: 9px;
    }
    .redColor{
        background-color: pink;
    }
    .greyColor{
        background-color: #ccc;
        
    }
</style>


