<template>
    <div class="box">
        <div>
            <span>热门标签</span>
            <span>点击即可查看本区标签的相关内容</span>
        </div>
        <div class="labelhidden" ref="labelList">
            <span @mouseover="addblue($event)" @mouseout="removeblue($event)">全部标签</span>
            <span v-for="(label,index) in labelList" @mouseover="addblue($event)" @mouseout="removeblue($event)">{{label.name}}</span>
        </div>
        <div class="jz">
            <span v-if="labelAllshow" @click='changeLabelList' @mouseover="addblueColor($event)" @mouseout="removeblueColor($event)">收起</span>
            <span v-else="labelAllshow" @click='changeLabelList' @mouseover="addblueColor($event)" @mouseout="removeblueColor($event)">全部</span>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'labelbar',
        data() {
            return {
                labelList: [{ name: 'BILIBLI正版' }, { name: 'OVA.OAD' }, { name: '剧场版' }, { name: '特典' }, { name: 'OVERLORD' }, { name: '日本动画' }, { name: '齐木楠雄的灾难' }, { name: 'FATE/STAYNIGHT-UBW-' }, { name: 'MADHOUSE' }, { name: '精灵梦可宝' }, { name: 'F宅字幕组' }, { name: '从零开始的异世界' }, { name: '火影忍者' }
                    , { name: '冈本信彦' }, { name: '日常' }, { name: '泡面番' }, { name: '小泽亚李' }, { name: '福山润' }, { name: '后宫' }, { name: '食戟之灵' }, { name: '卫宫士郎' }, { name: '四月是你的谎言' }],
                labelAllshow: false,

            }
        },
        methods: {
            addblue(e) {
                $(e.currentTarget).addClass('blue_buttom')
            },
            removeblue(e) {
                $(e.currentTarget).removeClass('blue_buttom')
            },
            addblueColor(e) {
                $(e.currentTarget).addClass('blue_color')
            },
            removeblueColor(e) {
                $(e.currentTarget).removeClass('blue_color')
            },
            changeLabelList() {
                let that = this.$refs.labelList;
                if (this.labelAllshow == false) {
                    $(that).animate({
                        height:'100px'
                    },1000)
                    this.labelAllshow=!this.labelAllshow
                }else{
                    $(that).animate({
                        height:'65px'
                    },1000)
                    this.labelAllshow=!this.labelAllshow
                }
            }
        }
    }
</script>
<style scoped>
    .box {
        text-align: left;
    }

    .box>div:first-child>span:first-child {
        display: inline-block;
        font-size: 18px;
        padding: 10px 5px;
        margin-bottom: 10px;
    }

    .box>div:first-child>span:nth-child(2) {
        font-size: 14px;
    }

    .labelhidden {
        overflow: hidden;
        height: 65px;
    }

    .box>div:nth-child(2)>span {
        display: inline-block;
        font-size: 12px;
        line-height: 20px;
        min-height: 20px;
        padding: 1px 10px;
        border-radius: 20px;
        border: 1px solid #cccccc;
        cursor: pointer;
        margin-right: 16px;
        margin-bottom: 10px;
    }

    .blue_buttom {
        border: 1px solid #6cc6e4 !important;
        color: #6cc6e4;
    }
    .blue_color {
        color: #6cc6e4;
    }

    .box>div:nth-child(3) {
        margin-top: 10px;
        border-top: 1px solid #cccccc;
    }

    .box>div:nth-child(3)>span {
        height: 30px;
        display: inline-block;
        margin-top: -10px;
        background: white;
        width: 100px;
        text-align: center;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        border-bottom: 1px solid #cccccc;
    }
</style>


