<template>
    <div class="box">
        <div style="padding-bottom: 8px">排行</div>
        <ul>
            <li v-for='(title,index) in num' :key='index'>
                <template v-if="index == 0 ">
                    <span class="redColor">{{index+1}}</span>
                    <div  @mouseover="detailshow(index)" @mouseout="detailclose(index)">
                        <div></div>
                        <div style="display: inline-block;">
                            <div>{{videotitle}}</div>
                            <div style="color: #ccc">综合评分：107.3万</div>
                        </div>
                    </div>
                </template>
                <template v-else-if="index < 3 && index > 0">
                    <span class="redColor">{{index+1}}</span>
                    <a href=""  @mouseover.self="detailshow(index)" @mouseout.self="detailclose(index)">
                        {{videotitle}}
                    </a>
                </template>
                <template v-else>
                    <span class="greyColor">{{index+1}}</span>
                    <a href=""  @mouseover.self="detailshow(index)" @mouseout.self="detailclose(index)">
                        {{videotitle}}
                    </a>
                </template>
                <!-- <template v-if="show[index].detail == true"> -->
                    <div class="detailedbox" v-show="show[index].detail===true">
                        <div>
                            <div>【凉风剧场】这部动画观众吵了十年，结局出乎意料！</div>
                            <div>
                                <span>凉风Kaze</span>
                                <span>2018-08-28 18:00</span>
                            </div>
                        </div>
                        <div>
                            <span></span>
                            <span>喜欢的话求个特别关注和硬币收藏啦~收藏过三万，下周开始更新这个系列解说 微博#凉风Kaze</span>
                        </div>
                        <div>
                            <span>78.5万</span>
                            <span>1.8万</span>
                            <span>2.4万</span>
                            <span>4.9万</span>
                        </div>
                    </div>
                <!-- </template> -->
            </li>
        </ul>
        <div class="more">查看更多>></div>
    </div>
</template>
<script>
    export default {
        name: 'rightnav',
        data() {
            return {
                videotitle: '【凉风剧场】这部动画观众吵了十年，结局出乎意料！',
                show: [{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                },{
                    detail:false
                }],
                showa: 0
            }
        },
        methods: {
            detailshow(index) {
                this.$forceUpdate();
                this.$set(this.show[index],"detail",true)
            },
            detailclose(index) {
                this.$forceUpdate();
                this.$set(this.show[index],"detail",false)
            }
        },
        props:['num']
    }
</script>
<style scoped>
    .box {
        width: 250px;
        display: inline-block;
        text-align: left;
        vertical-align: top;
        z-index: 100;
    }

    ul {
        margin: 0;
        padding: 0px;
        font-size: 12px;
    }

    li {
        position: relative;
        list-style: none;
        padding: 0px 0px 19px 0px;
        width: 100%;
        /* overflow: hidden; */
        text-overflow: clip;
        white-space: nowrap;
    }

    li>span {
        width: 15px;
        height: 15px;
        border-radius: 2px;
        display: inline-block;
        color: white;
        line-height: 16px;
        text-align: center;
        margin-right: 4px;
    }

    li>a {
        line-height: 16px !important;
        padding: 0 !important;
        width: 228px;
        display: inline-block;
        overflow: hidden;
        text-overflow: clip;
        white-space: nowrap;
    }

    .greyColor {
        background-color: #ccc
    }

    .redColor {
        background-color: pink
    }

    li>div:nth-child(2) {
        display: inline-block;
        vertical-align: top;
        overflow: hidden;
        text-overflow: clip;
        white-space: normal;
    }

    li>div:nth-child(2)>div:first-child {
        width: 80px;
        height: 50px;
        display: inline-block;
        background-color: aquamarine;
        border-radius: 5px;
    }

    li>div:nth-child(2)>div:nth-child(2) {
        width: 150px;
        height: 50px;
        vertical-align: top;
    }

    li>div:nth-child(2)>div:nth-child(2)>div:first-child {
        height: 35px;
        overflow: hidden;
    }

    .more {
        height: 26px;
        width: 100%;
        text-align: center;
        font-size: 14px;
        background-color: rgba(51, 51, 51, 0.2);
        border-radius: 2px;
        line-height: 26px;
        color: grey;
    }

    .detailedbox {
        position: absolute;
        left: 0;
        top: -185px;
        background-color: #66CCFF;
        width: 290px;
        padding: 10px;
        border-radius: 4px;
    }

    .detailedbox>div:first-child {
        width: 100%;
        overflow: hidden;
    }

    .detailedbox>div:first-child>div:first-child {
        padding: 8px 0;
    }

    .detailedbox>div:first-child>div:nth-child(2) {
        padding: 0 0 8px 0;
    }

    .detailedbox>div:nth-child(2) {
        padding: 15px 0;
        height: 50px;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
    }

    .detailedbox>div:nth-child(2)>span:first-child {
        height: 50px;
        width: 80px;
        background-color: aquamarine;
        border-radius: 5px;
        display: inline-block;
    }

    .detailedbox>div:nth-child(2)>span:nth-child(2) {
        display: inline-block;
        width: 200px;
        vertical-align: top;
        white-space: normal;
    }

    .detailedbox>div:nth-child(3) {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding-top: 8px;
    }
</style>


