import Vue from 'vue'
import Router from 'vue-router'
import home from '@/router/home'
import audioPage from '@/router/audioPage'
import gameCenter from '@/router/gameCenter'
import livePage from '@/router/livePage'
import memberPage from '@/router/memberPage'
import warPage from '@/router/warPage'
import BMLPage from '@/router/BMLPage'
import loadPage from '@/router/loadPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/audioPage',
      name: 'audioPage',
      component: audioPage
    },
    {
      path: '/gameCenter',
      name: 'gameCenter',
      component: gameCenter
    },
    {
      path: '/livePage',
      name: 'livePage',
      component: livePage
    },
    {
      path: '/memberPage',
      name: 'memberPage',
      component: memberPage
    },
    {
      path: '/warPage',
      name: 'warPage',
      component: warPage
    },
    {
      path: '/BMLPage',
      name: 'BMLPage',
      component: BMLPage
    },
    {
      path: '/loadPage',
      name: 'loadPage',
      component: loadPage
    },
  ]
})



