<template>
    <div class="box">
        <ul ref="ul">
            <li v-for="(img,index) in imgList" @mouseover="stop" @mouseout="play">
                <img src="../assets/15.jpg" alt="">
                <div>这是第{{index+1}}张图</div>
            </li>
        </ul>
        <div class="spanlist">
            <span v-for="(img,index) in imgList" :key="index" @click="selectImg(index)"></span>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'slideComponents',
        data() {
            return {
                timer: null, //定时器
                mark: 0, //当前展示的页面
                imgList: [1, 2, 3, 4, 5]
            }
        },
        methods: {
            autoplay() {
                this.mark++;
                if (this.mark >= this.imgList.length) {
                    this.mark = 0;
                }
            },
            play() {
                var that = this;
                // var newleft = -100*(that.mark+1)+'%';
                this.timer = setInterval(function () {
                    that.autoplay()
                    let ul = that.$refs.ul
                    $(ul).animate({
                        left: -100 * (that.mark) + '%',
                    }, 1000)
                    // console.log($('ul li')[0])
                }, 6000)
            },
            stop() {
                clearInterval(this.timer)
            },
            selectImg(index) {
                var that = this
                clearInterval(this.timer)
                this.mark = index;
                let ul = this.$refs.ul
                $(ul).animate({
                    left: -100 * (that.mark) + '%',
                }, 1000)
                this.play()
            }
        },
        created() {
            this.play();
        },
        beforeDestroy() {
            clearInterval(this.timer);//跳转页面前清除计时器
        }

    }
</script>
<style scoped>
    .box {
        width: 440px;
        height: 220px;
        overflow: hidden;
        position: relative;
        border-radius: 5px;
        display: inline-block;
    }

    ul {
        height: 100%;
        width: 500%;
        margin: 0;
        padding: 0;
        position: absolute;

    }

    li {
        position: relative;
        height: 100%;
        width: 20%;
        float: left;
        text-align: left;
        list-style: none;
        background-color: darkcyan;

    }
    img{
        height: 100%;
        width: 100%;
    }
    ul div {
        position: absolute;
        bottom: 0;
        width: 100%;
        height:25px;
        line-height: 25px;
        text-indent: 1em;
        color: white;
    }

    .spanlist {
        position: absolute;
        bottom: 0;
        right: 0;
        height: 25px;
        width: 120px;
        /* background-color: coral; */
        display: flex;
        justify-content: space-around;
        align-items: center
    }

    .spanlist span {
        height: 15px;
        width: 15px;
        border-radius: 100%;
        background-color: azure;
        display: inline-block;
    }
</style>


