<template>
    <div class="box">
        <div class="title">特别推荐</div>
        <div>
            <ul ref="tbtj">
                <li v-for="(img,index) in imglist">
                    <img src="../assets/1.jpg" alt="">
                    <div>
                        <span>这是第{{index+1}}张图</span>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'slide2',
        data() {
            return {
                timer:null,//定时器
                mark:0,//当前展示页面
                imglist:[1,2]
            }
        },
        methods:{
            autoplay() {
                this.mark++;
                if (this.mark >= this.imglist.length) {
                    this.mark = 0;
                }
            },
            play(){
                var that = this;
                this.timer = setInterval(function(){
                    that.autoplay()
                    let ul = that.$refs.tbtj
                    $(ul).animate({
                        left: -100 * (that.mark) + '%',
                    },1000)
                },6000)
            },
            stop() {
                clearInterval(this.timer)
            },
            selectImg(index) {
                var that = this
                clearInterval(this.timer)
                this.mark = index;
                let ul = this.$refs.tbtj
                $(ul).animate({
                    left: -100 * (that.mark) + '%',
                }, 1000)
                this.play()
            }
        },
        created() {
            this.play()
        },
        beforeDestroy() {
            clearInterval(this.timer)
        },
    }
</script>
<style scoped>
    .box{
        width: 250px;
        overflow: hidden;
        text-align: left;
        display: inline-block;
        vertical-align: top;
    }
    .title{
        padding-bottom: 8px;
    }
    .box>div:nth-child(2){
        height: 300px;
        width: 100%;
        position: relative;
    }
    ul{
        position: absolute;
        margin: 0;
        padding: 0;
        height: 100%;
        width: 200%;
        
    }
    li{
        list-style: none;
        height: 100%;
        width: 50%;
        /* background: yellow; */
        border-radius: 5px;
        overflow: hidden;
        float: left;
    }
    li img{
        width: 100%;
        height: 100%;
    }
    li span{
        position: absolute;
        bottom: 0;
        display: inline-block;
        margin: 8px 10px;
    }
</style>


