<template>
    <div class="box">
        <div class="videoshow">
        </div>
        <div @mouseout="closetitle">
            <div class="title" @mouseover="showtitle">{{title}}</div>
            <div ref="childIndex" >
                <span class="ready">{{ready}}</span>
                <span class="write">{{write}}</span>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'videoBox2',
        data() {
            return {
                title: '【歌姬手书】那什么的中日（英）V家歌姬（灵魂画师）',
                ready: '5373',
                write: '353',
            }
        },
        methods: {
            showtitle() {
                let a = this.$refs.childIndex;
                $(a).animate({
                    bottom: '-22px',
                }, 300)
            },
            closetitle(){
                let a = this.$refs.childIndex;
                $(a).animate({
                    bottom: '0px',
                }, 300)
            }
        },
        props: ['childIndex']
    }
</script>
<style scoped>
    .box {
        width: 160px;
        margin-right: 20px;
        margin-bottom: 15px;
        /* background-color: darkcyan; */
        display: inline-block;
    }

    .videoshow {
        width: 100%;
        height: 100px;
        background-color: aquamarine;
        border-radius: 5px;
    }

    .box>div:nth-child(2) {
        position: relative;
        overflow: hidden;
    }

    .title {
        padding-top: 8px;
        width: 100%;
        height: 40px;
        line-height: 20px;
        font-size: 12px;
        text-align: left;
    }

    .box>div:nth-child(2)>div:nth-child(2) {
        background-color: white;
        position: absolute;
        bottom: 0px;
        z-index: 10;
        width: 100%;
        height: 22px;
        text-align: left;
    }

    .ready,
    .write {
        color: rgb(51, 51, 51, 0.3);
        font-size: 12px;
        padding-left: 8px;
    }
</style>


